<template>
  <div class="find"></div>
</template>

<script>
import { Toast } from 'vant'
export default {
  data () {
    return {

    }
  },
  mounted () {
    this.recommend()
  },
  methods: {
    recommend () {
      let data = {
        uid: localStorage.getItem('userId'),
        offset: 10,
        limit: 30,
        timestamp: Date.parse(new Date())
      }
      this.api.recommend(data).then(res => {
        if (res.data.code == 200) {
          console.log(res)
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err.data.msg)
      })
    }
  }
}
</script>

